body{
    background-color: #F8F8F8;
    width: 100%;
    margin: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 85vh;
}

@media screen and (min-width: 900px) {
    body{
        width: 70%;
    }
}
@media screen and (min-width: 1300px) {
    body{
        width: 60%;
    }
}